import React from 'react';
import g from "glamorous";

const VisualDivider = g.hr({
  color: 'black',
  height: '1px',
  margin: '0 0 5px 0',
  padding: '0',
  display: 'block',
  border: '0',
  borderTop: '1px solid black',
  backgroundColor: 'unset',
});

export default VisualDivider



